import { useQuery } from "react-query";
import ApexChart from "react-apexcharts";
import { fetchCoinTickers } from "../api";
import { PriceData } from "./Coin";
import styled from "styled-components";

const Header = styled.header`
  height: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Title = styled.h1`
  font-size: 20px;
  color: ${(props) => props.theme.accentColor};
`;

const Overview = styled.div`
  display: flex;
  justify-content: space-around;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 10px 20px;
  border-radius: 10px;
  margin-bottom: 10px;
`;

const OverviewItem = styled.div`
  display: flex;
  flex-direction: column;

  align-items: center;

  span:first-child {
    font-size: 10px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 5px;
  }
`;

interface PriceProps {
  coinId: string;
  tickersData?: PriceData;
}

function Price({ coinId, tickersData }: PriceProps) {
  // Intl.NumberFormat : number --> string   ,     number      .
  //    -0.08      .
  return (
    <div>
      <Header>
        <Title>USD</Title>
      </Header>
      <Overview>
        <OverviewItem>
          <span>1  </span>
          <span>
            {tickersData
              ? new Intl.NumberFormat("en-US", {
                  style: "decimal",
                  minimumFractionDigits: 2,
                  maximumFractionDigits: 2,
                }).format(tickersData.quotes.USD.percent_change_1y)
              : null}
          </span>
        </OverviewItem>
        <OverviewItem>
          <span>1  </span>
          <span>
            {tickersData
              ? new Intl.NumberFormat("en-US", {
                  style: "decimal",
                  minimumFractionDigits: 2,
                  maximumFractionDigits: 2,
                }).format(tickersData.quotes.USD.percent_change_30d)
              : null}
          </span>
        </OverviewItem>
      </Overview>
      <Overview>
        <OverviewItem>
          <span>1  </span>
          <span>
            {tickersData
              ? new Intl.NumberFormat("en-US", {
                  style: "decimal",
                  minimumFractionDigits: 2,
                  maximumFractionDigits: 2,
                }).format(tickersData.quotes.USD.percent_change_7d)
              : null}
          </span>
        </OverviewItem>
        <OverviewItem>
          <span>  </span>
          <span>
            {tickersData
              ? new Intl.NumberFormat("en-US", {
                  style: "decimal",
                  minimumFractionDigits: 2,
                  maximumFractionDigits: 2,
                }).format(tickersData.quotes.USD.percent_change_24h)
              : null}
          </span>
        </OverviewItem>
      </Overview>
    </div>
  );
}
export default Price;
